@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700;800&family=Space+Grotesk:wght@500;600;700&family=JetBrains+Mono:wght@500;600&display=swap');

:root {
  /* Core palette — deep navy with vibrant accents */
  --bg-primary: #070B1E;
  --bg-secondary: #0D1333;
  --bg-card: #111842;
  --bg-card-hover: #151D50;
  --bg-elevated: #182058;
  
  /* Purple gradient spectrum */
  --purple-start: #7B2FE0;
  --purple-mid: #9B4DFF;
  --purple-end: #C084FC;
  --purple-glow: rgba(123, 47, 224, 0.25);
  
  /* Teal / cyan accents */
  --teal-start: #0D9488;
  --teal-mid: #14B8A6;
  --teal-end: #5EEBD0;
  --teal-glow: rgba(94, 235, 208, 0.2);
  
  /* Status colors */
  --success: #34D399;
  --warning: #FBBF24;
  --error: #F87171;
  --info: #60A5FA;
  
  /* Text */
  --text-primary: #F1F5F9;
  --text-secondary: #94A3B8;
  --text-tertiary: #64748B;
  --text-accent: var(--teal-end);
  
  /* Borders & surfaces */
  --border: rgba(255, 255, 255, 0.06);
  --border-active: rgba(123, 47, 224, 0.4);
  --glass: rgba(17, 24, 66, 0.6);
  --glass-border: rgba(255, 255, 255, 0.08);
  
  /* Shadows */
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 24px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 8px 40px rgba(0, 0, 0, 0.5);
  --shadow-glow-purple: 0 0 40px rgba(123, 47, 224, 0.2);
  --shadow-glow-teal: 0 0 40px rgba(94, 235, 208, 0.15);
  
  /* Typography */
  --font-display: 'Plus Jakarta Sans', sans-serif;
  --font-body: 'Plus Jakarta Sans', sans-serif;
  --font-mono: 'JetBrains Mono', monospace;
  
  /* Spacing */
  --sp-xs: 4px;
  --sp-sm: 8px;
  --sp-md: 16px;
  --sp-lg: 24px;
  --sp-xl: 32px;
  --sp-2xl: 48px;
  
  /* Radius */
  --r-sm: 8px;
  --r-md: 12px;
  --r-lg: 16px;
  --r-xl: 20px;
  --r-2xl: 24px;
  --r-full: 9999px;
  
  /* Transition */
  --ease: cubic-bezier(0.4, 0, 0.2, 1);
  --t-fast: 150ms var(--ease);
  --t-base: 250ms var(--ease);
  --t-slow: 400ms var(--ease);
}

/* ───── Reset ───── */
*, *::before, *::after { box-sizing: border-box; margin: 0; padding: 0; }
html { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }
body {
  font-family: var(--font-body);
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  min-height: 100dvh;
  overflow-x: hidden;
}

/* ───── Typography ───── */
h1, h2, h3, h4 {
  font-family: var(--font-display);
  font-weight: 700;
  line-height: 1.2;
  color: var(--text-primary);
}
h1 { font-size: 1.75rem; letter-spacing: -0.02em; }
h2 { font-size: 1.35rem; letter-spacing: -0.01em; }
h3 { font-size: 1.125rem; }
h4 { font-size: 1rem; }

.mono { font-family: var(--font-mono); }

/* ───── App Shell ───── */
.app-shell {
  max-width: 480px;
  margin: 0 auto;
  min-height: 100dvh;
  position: relative;
  padding-bottom: 80px;
}

.app-content {
  padding: var(--sp-md);
}

/* ───── Header ───── */
.app-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--sp-lg) var(--sp-md) var(--sp-md);
}

.header-user {
  display: flex;
  align-items: center;
  gap: var(--sp-md);
}

.avatar {
  width: 44px;
  height: 44px;
  border-radius: var(--r-full);
  background: linear-gradient(135deg, var(--purple-start), var(--teal-mid));
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1rem;
  color: white;
  flex-shrink: 0;
}

.header-greeting { font-size: 0.8rem; color: var(--text-tertiary); }
.header-name { font-size: 1rem; font-weight: 700; color: var(--text-primary); }

.header-actions { display: flex; gap: var(--sp-sm); }

.icon-btn {
  width: 40px;
  height: 40px;
  border-radius: var(--r-md);
  background: var(--bg-card);
  border: 1px solid var(--border);
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--t-fast);
  font-size: 1.1rem;
}
.icon-btn:hover {
  background: var(--bg-card-hover);
  color: var(--text-primary);
  border-color: var(--border-active);
}

/* ───── Total Balance Hero ───── */
.balance-hero {
  text-align: center;
  padding: var(--sp-lg) var(--sp-md);
}
.balance-label {
  font-size: 0.8rem;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-weight: 600;
  margin-bottom: var(--sp-xs);
}
.balance-amount {
  font-family: var(--font-mono);
  font-size: 2.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--teal-end), var(--purple-end));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.1;
}

/* ───── Cards ───── */
.card {
  background: var(--bg-card);
  border-radius: var(--r-xl);
  padding: var(--sp-lg);
  border: 1px solid var(--border);
  transition: all var(--t-base);
}
.card:hover {
  border-color: var(--glass-border);
  background: var(--bg-card-hover);
}

.card-glass {
  background: var(--glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: var(--r-xl);
  padding: var(--sp-lg);
}

/* Credit card style widget */
.credit-card {
  background: linear-gradient(135deg, var(--purple-start) 0%, #4F46E5 50%, var(--purple-mid) 100%);
  border-radius: var(--r-2xl);
  padding: var(--sp-xl);
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-glow-purple);
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.credit-card::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -30%;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: rgba(255,255,255,0.08);
}
.credit-card::after {
  content: '';
  position: absolute;
  bottom: -30%;
  left: -20%;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  background: rgba(255,255,255,0.05);
}

/* Wave decoration (SVG mountain/wave) */
.wave-decoration {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80px;
  overflow: hidden;
  z-index: 0;
}
.wave-decoration svg {
  position: absolute;
  bottom: 0;
  width: 100%;
}

/* ───── Circular Progress Ring ───── */
.ring-container {
  position: relative;
  width: 80px;
  height: 80px;
  flex-shrink: 0;
}
.ring-container svg {
  transform: rotate(-90deg);
  width: 100%;
  height: 100%;
}
.ring-bg {
  fill: none;
  stroke: var(--bg-secondary);
  stroke-width: 6;
}
.ring-fill {
  fill: none;
  stroke-width: 6;
  stroke-linecap: round;
  transition: stroke-dashoffset 1s var(--ease);
}
.ring-text {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-mono);
  font-size: 0.85rem;
  font-weight: 700;
  color: var(--text-primary);
}

/* ───── Pot Cards (list items) ───── */
.pot-item {
  display: flex;
  align-items: center;
  gap: var(--sp-md);
  padding: var(--sp-md) var(--sp-lg);
  background: var(--bg-card);
  border-radius: var(--r-lg);
  border: 1px solid var(--border);
  transition: all var(--t-base);
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
.pot-item:hover {
  background: var(--bg-card-hover);
  border-color: var(--border-active);
  transform: translateX(4px);
}
.pot-icon {
  width: 44px;
  height: 44px;
  border-radius: var(--r-md);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  flex-shrink: 0;
}
.pot-info { flex: 1; min-width: 0; }
.pot-name {
  font-weight: 600;
  font-size: 0.95rem;
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.pot-meta {
  font-size: 0.75rem;
  color: var(--text-tertiary);
  margin-top: 2px;
}
.pot-amount {
  font-family: var(--font-mono);
  font-weight: 700;
  font-size: 1rem;
  color: var(--text-primary);
  text-align: right;
  white-space: nowrap;
}

/* ───── Progress Bar ───── */
.progress-track {
  width: 100%;
  height: 6px;
  background: var(--bg-secondary);
  border-radius: var(--r-full);
  overflow: hidden;
  margin-top: var(--sp-sm);
}
.progress-fill {
  height: 100%;
  border-radius: var(--r-full);
  transition: width 0.8s var(--ease);
  background: linear-gradient(90deg, var(--teal-start), var(--teal-end));
}
.progress-fill.purple {
  background: linear-gradient(90deg, var(--purple-start), var(--purple-end));
}

/* ───── Bottom Tab Bar ───── */
.tab-bar {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 480px;
  background: var(--bg-secondary);
  border-top: 1px solid var(--border);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: var(--sp-sm) 0 calc(var(--sp-sm) + env(safe-area-inset-bottom, 0px));
  z-index: 500;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.tab-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3px;
  padding: var(--sp-sm) var(--sp-md);
  border: none;
  background: none;
  cursor: pointer;
  color: var(--text-tertiary);
  font-size: 0.65rem;
  font-weight: 600;
  font-family: var(--font-body);
  letter-spacing: 0.02em;
  transition: color var(--t-fast);
  min-width: 56px;
  -webkit-tap-highlight-color: transparent;
}
.tab-item.active {
  color: var(--teal-end);
}
.tab-item:hover {
  color: var(--text-secondary);
}
.tab-icon {
  font-size: 1.3rem;
  line-height: 1;
}

/* Center FAB in tab bar */
.tab-fab {
  width: 52px;
  height: 52px;
  border-radius: var(--r-full);
  background: linear-gradient(135deg, var(--purple-start), var(--purple-mid));
  border: none;
  color: white;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--t-base);
  box-shadow: 0 4px 20px rgba(123, 47, 224, 0.4);
  margin-top: -20px;
}
.tab-fab:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 28px rgba(123, 47, 224, 0.5);
}
.tab-fab:active {
  transform: scale(0.95);
}

/* ───── Section Title ───── */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--sp-md);
  padding: 0 var(--sp-xs);
}
.section-title {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--text-primary);
}
.section-link {
  font-size: 0.8rem;
  color: var(--text-accent);
  font-weight: 600;
  cursor: pointer;
  background: none;
  border: none;
  font-family: var(--font-body);
}

/* ───── Buttons ───── */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--sp-sm);
  padding: 14px 24px;
  min-height: 48px;
  font-family: var(--font-display);
  font-size: 0.9rem;
  font-weight: 600;
  border: none;
  border-radius: var(--r-lg);
  cursor: pointer;
  transition: all var(--t-fast);
  text-decoration: none;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
}
.btn-primary {
  background: linear-gradient(135deg, var(--purple-start), var(--purple-mid));
  color: white;
  box-shadow: 0 4px 16px rgba(123, 47, 224, 0.3);
}
.btn-primary:hover {
  box-shadow: 0 6px 24px rgba(123, 47, 224, 0.45);
  transform: translateY(-1px);
}
.btn-primary:active { transform: translateY(0); }

.btn-secondary {
  background: var(--bg-card);
  color: var(--text-secondary);
  border: 1px solid var(--border);
}
.btn-secondary:hover {
  border-color: var(--border-active);
  color: var(--text-primary);
  background: var(--bg-card-hover);
}

.btn-ghost {
  background: none;
  color: var(--text-secondary);
  border: none;
  padding: var(--sp-sm) var(--sp-md);
}
.btn-ghost:hover { color: var(--text-primary); }

.btn-teal {
  background: linear-gradient(135deg, var(--teal-start), var(--teal-mid));
  color: white;
  box-shadow: 0 4px 16px rgba(94, 235, 208, 0.2);
}

/* ───── Forms ───── */
.form-group { margin-bottom: var(--sp-lg); }
.form-label {
  display: block;
  font-weight: 600;
  font-size: 0.75rem;
  color: var(--text-tertiary);
  margin-bottom: var(--sp-sm);
  text-transform: uppercase;
  letter-spacing: 0.08em;
}
.form-input, .form-select, .form-textarea {
  width: 100%;
  min-height: 48px;
  padding: 12px 16px;
  font-family: var(--font-body);
  font-size: 0.95rem;
  color: var(--text-primary);
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: var(--r-md);
  transition: all var(--t-fast);
  outline: none;
}
.form-input:focus, .form-select:focus, .form-textarea:focus {
  border-color: var(--purple-start);
  box-shadow: 0 0 0 3px var(--purple-glow);
}
.form-input::placeholder { color: var(--text-tertiary); }

/* ───── Tables ───── */
.data-table {
  width: 100%;
  border-collapse: collapse;
}
.data-table thead th {
  padding: var(--sp-md);
  text-align: left;
  font-size: 0.7rem;
  font-weight: 700;
  color: var(--text-tertiary);
  text-transform: uppercase;
  letter-spacing: 0.08em;
  border-bottom: 1px solid var(--border);
  white-space: nowrap;
}
.data-table tbody td {
  padding: var(--sp-md);
  font-size: 0.85rem;
  border-bottom: 1px solid var(--border);
  white-space: nowrap;
  color: var(--text-secondary);
}
.data-table tbody tr:hover td { background: var(--bg-card-hover); }
.amount-positive { color: var(--success); font-family: var(--font-mono); font-weight: 600; }
.amount-negative { color: var(--error); font-family: var(--font-mono); font-weight: 600; }

/* ───── Modal Overlay ───── */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(7, 11, 30, 0.85);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: flex-end;
  justify-content: center;
  z-index: 1000;
  padding: 0;
  animation: fadeIn 0.2s var(--ease);
}
.modal-sheet {
  width: 100%;
  max-width: 480px;
  max-height: 90dvh;
  background: var(--bg-secondary);
  border-top-left-radius: var(--r-2xl);
  border-top-right-radius: var(--r-2xl);
  padding: var(--sp-lg);
  overflow-y: auto;
  animation: slideUp 0.3s var(--ease);
}
.modal-handle {
  width: 36px;
  height: 4px;
  background: var(--text-tertiary);
  border-radius: var(--r-full);
  margin: 0 auto var(--sp-lg);
}

@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@keyframes slideUp { from { transform: translateY(100%); } to { transform: translateY(0); } }

/* ───── Transaction type toggle ───── */
.toggle-group {
  display: flex;
  background: var(--bg-primary);
  border-radius: var(--r-md);
  padding: 3px;
  gap: 3px;
}
.toggle-option {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: var(--r-sm);
  font-family: var(--font-body);
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--t-fast);
  background: transparent;
  color: var(--text-tertiary);
}
.toggle-option.active {
  background: linear-gradient(135deg, var(--purple-start), var(--purple-mid));
  color: white;
  box-shadow: 0 2px 8px rgba(123, 47, 224, 0.3);
}

/* ───── Empty state ───── */
.empty-state {
  text-align: center;
  padding: var(--sp-2xl) var(--sp-lg);
  color: var(--text-tertiary);
}
.empty-state-icon { font-size: 3rem; margin-bottom: var(--sp-md); opacity: 0.5; }
.empty-state-text { font-size: 0.9rem; line-height: 1.5; }

/* ───── Skeleton ───── */
.skeleton {
  background: linear-gradient(90deg, var(--bg-card) 0%, var(--bg-card-hover) 50%, var(--bg-card) 100%);
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s infinite;
  border-radius: var(--r-lg);
}
@keyframes skeleton-loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* ───── Login page ───── */
.login-page {
  min-height: 100dvh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--sp-md);
  background: var(--bg-primary);
  position: relative;
  overflow: hidden;
}
.login-page::before {
  content: '';
  position: absolute;
  top: -200px;
  right: -200px;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(123, 47, 224, 0.15), transparent 70%);
}
.login-page::after {
  content: '';
  position: absolute;
  bottom: -300px;
  left: -200px;
  width: 600px;
  height: 600px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(94, 235, 208, 0.1), transparent 70%);
}
.login-card {
  width: 100%;
  max-width: 380px;
  z-index: 1;
}
.login-brand {
  text-align: center;
  margin-bottom: var(--sp-xl);
}
.login-logo {
  font-size: 3rem;
  margin-bottom: var(--sp-md);
}
.login-title {
  font-size: 1.75rem;
  font-weight: 800;
  margin-bottom: var(--sp-xs);
  background: linear-gradient(135deg, var(--teal-end), var(--purple-end));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.login-subtitle {
  font-size: 0.85rem;
  color: var(--text-tertiary);
}

.error-badge {
  padding: var(--sp-md);
  background: rgba(248, 113, 113, 0.1);
  border: 1px solid rgba(248, 113, 113, 0.3);
  border-radius: var(--r-md);
  color: var(--error);
  font-size: 0.85rem;
  margin-bottom: var(--sp-md);
}

.cred-hint {
  margin-top: var(--sp-lg);
  padding: var(--sp-md);
  background: var(--bg-card);
  border-radius: var(--r-md);
  border: 1px solid var(--border);
  font-size: 0.8rem;
  color: var(--text-tertiary);
}
.cred-hint strong { color: var(--text-secondary); }

/* ───── Utilities ───── */
.flex { display: flex; }
.flex-col { flex-direction: column; }
.items-center { align-items: center; }
.justify-between { justify-content: space-between; }
.justify-center { justify-content: center; }
.gap-xs { gap: var(--sp-xs); }
.gap-sm { gap: var(--sp-sm); }
.gap-md { gap: var(--sp-md); }
.gap-lg { gap: var(--sp-lg); }
.text-center { text-align: center; }
.text-right { text-align: right; }

.mt-sm { margin-top: var(--sp-sm); }
.mt-md { margin-top: var(--sp-md); }
.mt-lg { margin-top: var(--sp-lg); }
.mt-xl { margin-top: var(--sp-xl); }
.mb-sm { margin-bottom: var(--sp-sm); }
.mb-md { margin-bottom: var(--sp-md); }
.mb-lg { margin-bottom: var(--sp-lg); }
.mb-xl { margin-bottom: var(--sp-xl); }

.stack { display: flex; flex-direction: column; gap: var(--sp-md); }

/* ───── Responsive ───── */
@media (max-width: 480px) {
  h1 { font-size: 1.5rem; }
  .balance-amount { font-size: 2.2rem; }
  .app-content { padding: var(--sp-sm) var(--sp-md); }
  .credit-card { min-height: 160px; padding: var(--sp-lg); }
}

/* ───── Scrollbar ───── */
::-webkit-scrollbar { width: 4px; }
::-webkit-scrollbar-track { background: transparent; }
::-webkit-scrollbar-thumb { background: var(--border); border-radius: var(--r-full); }

/* Safe area padding for iOS */
@supports (padding-bottom: env(safe-area-inset-bottom)) {
  .app-shell { padding-bottom: calc(80px + env(safe-area-inset-bottom)); }
}
